import { createConnection } from "mysql";
import SecretsManager from "aws-sdk/clients/secretsmanager";
import { readFileSync } from "fs";
import { join } from "path";

const secrets = new SecretsManager({});

export async function handler(e) {
  try {
    const { config } = e.params;
    const { password, username, host } = await getSecretValue(
      config.credsSecretName
    );
    const connection = createConnection({
      host,
      user: username,
      password,
      multipleStatements: true,
    });

    connection.connect();

    // eslint-disable-next-line no-undef
    const sqlScript = readFileSync(join(__dirname, "script.sql")).toString();
    const res = await query(connection, sqlScript);

    return {
      status: "OK",
      results: res,
    };
  } catch (err) {
    return {
      status: "ERROR",
      err,
      message: err.message,
    };
  }
}

function query(connection, sql) {
  return new Promise((resolve, reject) => {
    connection.query(sql, (error, res) => {
      if (error) return reject(error);

      return resolve(res);
    });
  });
}

function getSecretValue(secretId) {
  return new Promise((resolve, reject) => {
    secrets.getSecretValue({ SecretId: secretId }, (err, data) => {
      if (err) return reject(err);

      return resolve(JSON.parse(data.SecretString));
    });
  });
}
